<script>
/* global Stream */
import { mapState } from "vuex";
import NoSubscription from "./NoSubscription.vue";
export default {
  components: { NoSubscription },
  name: "VideopPlayer",
  data: () => ({
    currentVideo: {},
    videos: [],
    category: {},
    lazyImages: [],
  }),
  computed: {
    ...mapState({
      isLoading: (state) => state.isLoading,
      user: (state) => state.user || {},
    }),
    mustSubscribe() {
      return (
        this.currentVideo.is_free == 0 && this.empty(this.user.subscription)
      );
    },
  },
  beforeCreate() {
    this.$loadScript(
      "https://embed.videodelivery.net/embed/sdk.latest.js"
    ).then(() => {
      this.embedSDKLoaded = true;
      let uid = this.$route.params.uid;
      let categorySlug = this.$route.params.categorySlug;
      this.$http.get(
          `ghost/tvplus/streaming/videos/${categorySlug}/${uid}`
        )
        .then((response) => {
          if (response.data.status) {
            this.videos = response.data.videos;
            this.category = response.data.category;
            this.currentVideo = this.videos.splice(0, 1)[0];
            let iframePlayer = document.querySelector("#iFrameStreamPlayer");
            if (iframePlayer) {
              if (this.embedSDKLoaded) {
                this.streamPlayer = true;
                const streamPlayer = Stream(
                  document.querySelector("#iFrameStreamPlayer")
                );
                streamPlayer
                  .play()
                  .then(() => {
                    streamPlayer.muted = false;
                  })
                  .catch(() => {
                    streamPlayer.muted = true;
                    streamPlayer.play();
                  });
              }
            }
          }
        });
    });
  },
  methods: {
    changeVideo(video, index) {
      console.log(index);
      return new Promise((resolve) => {
        this.$store.commit("SET_REQUEST_FLAG", "Changing_video");
        this.$store.state.isLoading = true;
        this.videos.push(this.currentVideo);
        this.currentVideo = video;
        this.videos.splice(index, 1);
        resolve(true);
      });
    },
    testImageUrl(url) {
      return new Promise(function (resolve, reject) {
        var image = new Image();
        image.addEventListener("load", () => resolve(true));
        image.addEventListener("error", () => reject(false));
        image.src = url;
      });
    },
    checkFoundImages(imageUrl, i) {
      this.lazyImages.push({ id: i, status: "loading" });
      return this.testImageUrl(imageUrl)
        .then(() => {
          this.lazyImages[i].status = true;
        })
        .catch(() => {
          this.lazyImages[i].status = false;
        });
    },
    imageFound(i) {
      if (this.lazyImages[i]) {
        return this.lazyImages[i].status == true;
      }
      return false;
    },
  },
  watch: {
    $route() {
      let filterKey = this.$route.params.uid;
      filterKey = filterKey && filterKey.toLowerCase();
      var records = this.videos;
      if (filterKey) {
        records = records.filter((row) => {
          return Object.keys(row).some((key) => {
            return String(row[key]).toLowerCase().indexOf(filterKey) > -1;
          });
        });
      }
      var index = this.videos.indexOf(records[0]);
      this.changeVideo(records[0], index).then(() => {
        this.$store.commit("SET_REQUEST_FLAG", null);
        this.$store.state.isLoading = false;
      });
    },
    videos() {
      this.lazyImages = [];
      this.videos.forEach((item, i) => {
        this.checkFoundImages(item.thumb_url, i);
      });
    },
  },
};
</script>
