                    ></path>
                  </svg>
                </div>
              </div>

              <div class="video-thumbnail__content">
                <div class="video-thumbnail__title">
                  {{ video.title }}
                </div>

                <div class="video-thumbnail__meta">
                  <div class="video-thumbnail__duration">
                    <svg
                      class="video-thumbnail__duration-icon"
                      viewBox="0 0 70 88"
                    >
                      <path
                        d="M0 7.31C0 .55 4.58-2 10.25 1.67l55.5 35.73c5.66 3.64 5.67 9.55 0 13.2l-55.5 35.73C4.59 90 0 87.46 0 80.69z"
                      ></path>
