                  <div class="my-list-cta js-my-list-cta is-visible">
                    <div
                      class="my-list-cta__button js-my-list-cta-button"
                      tabindex="0"
                    >
                      <svg class="my-list-cta__circle" viewBox="0 0 100 100">
                        <circle
                          class="my-list-cta__circle-icn"
                          cx="50"
