  </div>
</template>
<script>
import LazyImage from "@/components/LazyImage.vue";
export default {
  components: { LazyImage },
  name: "CategoryVideos",
  data: () => ({
    videos: [],
    category: {},
    emptyResult: false,
  }),
  created() {
    this.getVideos();
  },
  watch: {
    $route() {
      this.getVideos();
    },
  },
  methods: {
    getVideos() {
      let categorySlug = this.$route.params.categorySlug;
      this.$http.get(`ghost/tvplus/streaming/videos/${categorySlug}`)
        .then((response) => {
          if (response.data.status) {
            this.videos = response.data.videos;
            this.category = response.data.category;
            if (this.empty(this.videos)) {
              this.emptyResult = true;
            }
          }
        });
    },
  },
};
</script>
